@use "../bootstrap.scss" as *;

.logo {
  &:hover {
    opacity: 0.8;

    @include transition(0.1s, opacity);
  }

  &:active {
    opacity: 0.6;
  }
}

.logo__image {
  display: block;
  width: 33px;
  height: 38px;

  @media (min-width: $tablet-width) {
    width: 174px;
    height: 50px;
  }

  @media (min-width: $desktop-width) {
    width: 202px;
    height: 59px;
  }
}
