@use "../bootstrap.scss" as *;

.details__title {
  margin-bottom: 34px;

  @media (min-width: $tablet-width) {
    margin-bottom: 57px;
  }
}

.details__list {
  display: grid;
  row-gap: 12px;

  @media (min-width: $tablet-width) {
    grid-template-columns: repeat(2, 1fr);
    gap: 32px 61px;
  }

  @media (min-width: $desktop-width) {
    grid-column: 1 / -1;
    grid-template-columns: repeat(4, 1fr);
    column-gap: 73px;
  }
}
